.section-filters {
  padding-bottom: 3.75rem;
}
@media screen and (min-width: 63.99875rem) {
  .section-filters {
    padding-bottom: 6.25rem;
  }
}
.section-filters .filters-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
}
.section-filters .filters-content-wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
}
@media screen and (min-width: 63.99875rem) {
  .section-filters .filters-content-wrapper {
    flex-direction: row;
  }
}
.section-filters .filters-wrapper {
  width: 100%;
}
@media screen and (min-width: 63.99875rem) {
  .section-filters .filters-wrapper {
    width: 30%;
  }
}
.section-filters .filters-wrapper .btn-wrapper {
  margin-top: 1.5rem;
  gap: 1.25rem;
}
@media screen and (min-width: 47.99875rem) {
  .section-filters .filters-wrapper .btn-wrapper {
    gap: 2rem;
  }
}
.section-filters .filters-wrapper .btn-wrapper .button.button-secondary {
  box-shadow: 0 0.1875rem 0.625rem -0.1875rem var(--text-color);
}
.section-filters .btn-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.section-filters .btn-wrapper .button.button-secondary {
  width: 100%;
}
@media screen and (min-width: 47.99875rem) {
  .section-filters .btn-wrapper .button.button-secondary {
    width: auto;
  }
}
.section-filters .card-wrapper {
  width: 100%;
  margin-top: 2.25rem;
}
@media screen and (min-width: 63.99875rem) {
  .section-filters .card-wrapper {
    margin-left: 2rem;
    margin-top: 0;
    width: calc(70% - 2rem);
  }
}
@media screen and (min-width: 77.49875rem) {
  .section-filters .card-wrapper {
    margin-left: 3.3125rem;
    width: calc(70% - 3.3125rem);
  }
}
.section-filters .card-wrapper .btn-wrapper {
  margin-top: 2.5rem;
}
@media screen and (min-width: 47.99875rem) {
  .section-filters .card-wrapper .btn-wrapper {
    margin-top: 3.4375rem;
  }
}
.section-filters .title-wrapper {
  width: 100%;
  max-width: 68.1875rem;
  padding-bottom: 1rem;
}
@media screen and (min-width: 47.99875rem) {
  .section-filters .title-wrapper {
    padding-bottom: 1.875rem;
  }
}
.section-filters .title-wrapper .h1 {
  text-align: center;
}
.section-filters .form-title-wrapper {
  margin-top: 2rem;
}
.section-filters .form-title-wrapper h5 {
  letter-spacing: 0.09375rem;
}
@media screen and (min-width: 63.99875rem) {
  .section-filters .form-title-wrapper h5 {
    margin-bottom: 1.25rem;
  }
}
.section-filters .search-wrapper {
  display: flex;
  align-items: center;
  width: 100%;
  border: 0.0625rem solid var(--secondary-color);
  border-radius: 1.25rem;
  padding: 0.375rem 2.875rem;
}
.section-filters .search-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 50%;
  left: 1rem;
  transform: translateY(-50%);
  width: 1.5rem;
  height: 1.5rem;
}
.section-filters .field-select-wrapper {
  padding: 0 1.5rem;
  border-radius: 0.75rem;
  box-shadow: 0 0.1875rem 0.625rem -0.125rem var(--text-color);
}
.section-filters .field-select {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 0.75rem 0;
}
.section-filters .field-sort {
  padding: 1.25rem 0.75rem;
}
@media screen and (min-width: 63.99875rem) {
  .section-filters .field-sort {
    padding: 0.4375rem 0.75rem 0.875rem;
  }
}
.section-filters .search-field,
.section-filters .input-search {
  width: 100%;
}
.section-filters .card-grid {
  display: grid;
  grid-template-columns: 1fr;
  column-gap: 1.25rem;
  row-gap: 1.25rem;
}
@media screen and (min-width: 47.99875rem) {
  .section-filters .card-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (min-width: 77.49875rem) {
  .section-filters .card-grid {
    column-gap: 2.5rem;
    row-gap: 2.5rem;
  }
}
.section-filters .movie-card {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  padding: 1.5rem 1.125rem 1.875rem;
  box-shadow: 0px 0.25rem 0.625rem 0 var(--text-color);
}
.section-filters .movie-card,
.section-filters .img-wrapper {
  border-radius: 0.75rem;
}
.section-filters .img-wrapper {
  overflow: hidden;
  position: relative;
  width: 100%;
  height: 90vw;
  min-height: 18.75rem;
  max-height: 26.875rem;
}
@media screen and (min-width: 63.99875rem) {
  .section-filters .img-wrapper {
    height: 40vw;
    max-height: 33.4375rem;
  }
}
.section-filters .img-wrapper .total-wrap {
  position: absolute;
  top: 0.4375rem;
  left: 0.625rem;
  width: auto;
}
.section-filters .img-wrapper .total-wrap.total-rating-wrap .total.total-rating {
  padding: 0.375rem 0.625rem;
}
.section-filters .img-wrapper .total-wrap.total-rating-wrap .total.total-rating .total-text {
  font-size: 0.875rem;
  line-height: 1.125rem;
  font-weight: 700;
}
.section-filters .img-wrapper .total-wrap.total-rating-wrap .total.total-rating .rating-icon {
  width: 0.875rem;
  height: 0.875rem;
  transform: translateY(-0.0625rem);
}
.section-filters picture {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.section-filters .movie-card-img {
  height: 100%;
  object-fit: cover;
}
.section-filters .movie-card-link-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
}
.section-filters .movie-card-link-wrapper a {
  color: transparent;
  font-size: 0;
  line-height: 0;
  width: 100%;
  height: 100%;
}
.section-filters .movie-content-wrapper,
.section-filters .btn-wrap {
  margin-top: 1.4375rem;
}
.section-filters .card-title-wrapper .h5 {
  text-align: center;
}
.section-filters .card-title-wrapper .card-btn-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.section-filters .button-preloader-wrap.button-preloader-wrap-search {
  width: 100%;
}
.section-filters .button-preloader-wrap.button-preloader-wrap-search .lds-ring {
  color: var(--secondary-color);
  top: 50%;
  left: unset;
  right: 1.25rem;
  transform: translateY(-50%);
}
.section-filters .button-preloader-wrap.button-preloader-wrap-search .lds-ring {
  width: 1.375rem;
  height: 1.375rem;
}
.section-filters .button-preloader-wrap.button-preloader-wrap-search .lds-ring span {
  width: 1.125rem;
  height: 1.125rem;
  margin: 0.125rem;
  border: 0.125rem solid var(--secondary-color);
  border-color: var(--secondary-color) transparent transparent transparent;
}